.quiz-play-cont {
  font-size: 10px;

  width: 100vw;
  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background-color: blue;
}

.quiz-play {
  width: 95%;
  height: 96%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  font-family: averta, serif; /*Averta*/

  --prime-color: rgb(250, 250, 250);
  --second-color: blue;
}

.quiz-play-question {
  width: 100%;
  height: 45%;

  margin-bottom: 5px;
  padding: 5px;

  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  font-size: 4em;

  background-color: var(--prime-color);
  color: var(--second-color);
  border-radius: 2px;
  box-shadow: rgba(0, 0, 0, 0.3) 0 5px 10px 0px;
}

.quiz-play-options {
  width: 100%;
  height: 40%;

  margin-bottom: 5px;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  overflow-y: auto;
  overflow-x: hidden;
}

.quiz-play-option {
  width: 48%;
  margin: 8px 0px;
  padding: 5px;

  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  font-size: 3em; /*2.5em*/

  background-color: var(--prime-color);

  border-radius: 2px;
  box-shadow: rgba(0, 0, 0, 0.3) 0 5px 10px 0;

  color: blue;
  cursor: pointer;
}

.quiz-play-option:hover {
  background-color: rgb(235, 235, 235);
}

.quiz-play-option:active {
  background-color: rgb(210, 210, 210);
}

.quiz-play-footer {
  width: 100%;
  height: calc(10% - 10px);

  display: flex;
  align-items: center;
  justify-content: space-between;

  background-color: transparent;
  color: var(--second-color);
}

.quiz-play-footer-text {
  font-size: 2em;
  color: var(--prime-color);
}

.quiz-play-footer-btn {
  color: white;
  background-color: transparent;
  padding: 5px 10px;
  font-family: bree;
  border: 2px solid white;
  border-radius: 2px;
  font-size: 1.5em;
  cursor: pointer;
}

.quiz-play-footer-btn:hover {
  background-color: var(--prime-color);
  color: var(--second-color);
}

.quiz-play-footer-btn:active {
  background-color: rgb(220, 220, 220);
}

.quiz-play-option-right {
  color: white;
  background-color: #00cc66;
}

.quiz-play-option-right:hover {
  color: white;
  background-color: #00cc66;
}

.quiz-play-option-wrong {
  background-color: #ed1c24;
  color: white;
}

.quiz-play-option-wrong:hover {
  background-color: #ed1c24;
  color: white;
}

@media only screen and (max-width: 500px) {
  .quiz-play-cont {
    font-size: 8px;
  }
}
