.quiz-create-cont {
  font-size: 10px;
  width: 100vw;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  --prime-color: blue;
  --second-color: white;

  background-color: var(--prime-color);

  display: flex;
  flex-direction: column;
  justify-content: center;
}

* {
  border: 0px solid black;
}

.quiz-create {
  width: 60%;
  height: 88%;

  display: flex;
  align-items: center;
  flex-direction: column;
  overflow-y: auto;
}

.quiz-create-question {
  font-size: 3em;
  width: 100%;
  min-height: 4em;
  max-height: 5em;

  margin-top: 15px;
  margin-bottom: 15px;
}

.quiz-create-option-cont {
  width: 100%;
  height: 14em;
  margin-bottom: 15px;
}

.quiz-create-option-cont textarea {
  width: 100%;
  height: 65%;

  font-size: 2.5em;
}

.quiz-create-option-btn {
  font-size: 2em;
  margin-top: 2px;
  font-family: bree;
  border-radius: 4px;
  color: var(--prime-color);
  background-color: var(--second-color);
  cursor: pointer;
}

.quiz-create-option-btn:hover {
  background-color: rgb(250, 250, 250);
}

.quiz-create-option-btn:active {
  background-color: rgb(240, 240, 240);
}

.quiz-create-option-btn-large {
  width: 100%;
  height: 32%;
}

.quiz-create-option-btn-small {
  width: 49%;
  height: 32%;
  margin: 2px 2px;
}

.quiz-create-option {
  font-size: 2.5em;
  width: 100%;
  max-height: 4em;

  border-radius: 2px;
  margin-bottom: 10px;
}

.quiz-create-option text-area {
  width: 100%;
  height: 100%;
}

.quiz-create-textarea {
  padding: 10px;
  border-radius: 4px;

  font-family: bree;
  color: blue;

  text-align: center;

  height: max-content;

  resize: none;

  background-color: var(--second-color);
}

.quiz-create-btns-cont {
  width: 90%;
  height: 10%;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  border-top: 2px dashed white;
}

.quiz-create-btns-cont button {
  padding: 5px 10px;
  border-radius: 2px;
  background-color: transparent;
  color: white;
  border: 2px solid white;
  font-family: bree;
  font-size: 1.8em;
  cursor: pointer;
  width: max-content;
}

.quiz-create-btns-cont button:hover {
  background-color: white;
  color: blue;
}

.quiz-create-btns-cont button:active {
  background-color: rgb(230, 230, 230);
}

@media only screen and (max-width: 1000px) {
  .quiz-create {
    width: 90%;
  }
}

@media only screen and (max-width: 500px) {
  .quiz-create-cont {
    font-size: 9px;
  }
}
