.quiz-create-info-bg {
  width: 100vw;
  height: 100vh;
  background-color: blue;

  display: flex;
  align-items: center;
  justify-content: center;
}

.quiz-create-info-cont {
  width: 80%;
  height: 80%;
  background-color: white;
  margin: 0 auto;

  font-size: 15px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  box-shadow: rgba(0, 0, 0, 0.2) -5px 5px 10px 0px;
}

.quiz-create-info-title {
  font-family: governor;
  color: blue;
  font-size: 3em;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 20px;
  border-bottom: 2px solid blue;
}

.quiz-create-info-label-cont {
  width: 100%;
  height: 60%;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.quiz-create-info-label {
  font-size: 2em;
  min-width: 18em;
  width: 70%;
  margin: 10px auto;
  padding: 10px;

  border-bottom: 2px dashed blue;
  border-radius: 2px;

  color: blue;
  font-family: averta;
  background-color: white;

  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.quiz-create-info-label p {
  display: inline-block;

  width: 18%;
  font-size: 1em;
  margin-bottom: 5px;
}

.quiz-create-info-label input {
  width: 75%;
  height: 98%;
  font-size: 1em;
  color: blue;
  font-family: averta;
  padding: 0px 5px;
}

.quiz-create-info-btns {
  width: 70%;
  height: 3em;

  display: flex;
  align-items: center;
  justify-content: space-around;
}

.quiz-create-info-btn {
  width: 5em;
  height: 2.5em;
  font-size: 1em;
  font-family: bree;
  color: blue;
  background-color: white;
  border: 2px solid blue;
  border-radius: 2px;
  margin-top: 20px;
  cursor: pointer;
}

.quiz-create-info-btn:hover {
  background-color: blue;
  color: white;
}

.quiz-create-info-btn:active {
  background-color: rgb(4, 4, 221);
}

@media only screen and (max-width: 720px) {
  .quiz-create-info-cont {
    font-size: 12px;
  }
}

@media only screen and (max-width: 600px) {
  .quiz-create-info-cont {
    width: 100%;
    height: 70%;
  }
}

@media only screen and (max-width: 500px) {
  .quiz-create-info-cont {
    font-size: 10px;
  }
  .quiz-create-info-label {
    flex-direction: column;
  }
  .quiz-create-info-label input {
    text-align: center;
  }
}

@media only screen and (max-height: 600px) {
  .quiz-create-info-cont {
    font-size: 12px;
  }
}

@media only screen and (max-height: 500px) {
  .quiz-create-info-cont {
    height: 100%;
  }
}

@media only screen and (max-height: 400px) {
  .quiz-create-info-cont {
    justify-content: flex-start;
    font-size: 8px;
  }
}
