.quiz-info {
  font-family: bree;
  font-size: 9px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  overflow-y: auto;
  overflow-x: hidden;

  width: 100vw;
  height: 100vh;

  --back-color: blue;
  --acc-color: white;

  background-color: var(--back-color);
  color: var(--back-color);
}

.quiz-info * {
  position: relative;
  z-index: 1;
}

.quiz-info-subs {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 25px;
}

.quiz-info-text-main {
  font-size: 5em;
  /*width: 20em;*/
  width: 80%;
  height: 3em;
  height: max-content;
  margin-top: 50px;
  text-align: center;
  padding: 10px 15px 10px 15px;
  background-color: var(--acc-color);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 10px 10px 0;
  border-radius: 2px;
  word-wrap: break-word;
}

.quiz-info-text-sub {
  font-size: 3em;
  min-width: 20%;
  width: max-content;
  height: max-content;
  padding: 10px;
  margin: 10px 25px 10px 0;
  text-align: center;
  background-color: var(--acc-color);
  border-radius: 2px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 10px 10px 0;
}

.quiz-info-btns {
  width: 100em;
  height: 12em;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  color: var(--back-color);
}

.quiz-info-btn {
  font-size: 2em;
  height: 2.5em;
  width: 6em;
  margin: 20px;
  border: 0;
  border-radius: 2px;
  background-color: var(--acc-color);
  color: var(--back-color);
  font-family: bree;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 10px 0;
  transition: box-shadow 0.2s, transform 0.2s;
}

.quiz-info-btn:hover {
  box-shadow: rgb(0, 0, 0, 0.3) 0px 10px 10px 0;
  transform: translateY(-5px);
}

.quiz-info-btn:active {
  background-color: rgb(200, 200, 200, 200);
  color: blue;
}

@media only screen and (max-width: 900px) {
  .quiz-info-btns {
    width: 95%;
    height: max-content;
  }
}

@media only screen and (max-width: 500px) {
  .quiz-info {
    font-size: 7px;
  }
}

@media only screen and (max-height: 500px) {
  .quiz-info {
    font-size: 7px;
  }
}
