.navbar {
  font-size: 15px;
  width: 100%;
  height: 4.5em; /*70px*/
  display: flex;
  background-color: rgb(247, 247, 247);
  box-shadow: rgba(0, 0, 0, 0.25) 0 0 15px 0;
  align-items: center;
  justify-content: space-around;

  position: relative;
  z-index: 2;
}

.navbar-logo {
  width: 12rem;
  height: 2.2rem;
  text-align: center;
  font-family: governor;
  font-size: 2em;
  color: blue /*#4571e8*/;
}

.navbar-search {
  display: flex;
  height: 4em /*75%*/;
  width: 70%;
  align-items: center;
  justify-content: center;
}

.navbar-search-field {
  height: 75%;
  width: 50%;
  min-width: 200px;
  padding: 10px;
  border: 0;
  background-color: transparent;
  border-bottom: 2px solid #4571e8;
  color: blue;
  font-family: bree;
  font-size: 1.8em;
}

.navbar-search-field:focus {
  border-bottom-color: blue;
}

.navbar-search-icon {
  width: 2.5em;
  height: 2.5em;
  margin-left: 0.3em;
  margin-top: 0.5em;
  padding: 0.3em;
  cursor: pointer;
  border-radius: 1.1em;
}

.navbar-search-icon:hover {
  background-color: rgba(0, 64, 255, 0.2);
}
.navbar-search-icon:active {
  background-color: rgba(0, 64, 255, 0.3);
}

.navbar-options {
  width: 10%;
  min-width: 80px;
  height: 75%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border: 0px solid black;
}

.navbar-options-icon {
  width: 2.2em;
  padding: 0.3em;
  border-radius: 1em;
  cursor: pointer;
}

.navbar-options-icon:hover {
  background-color: rgba(0, 64, 255, 0.2);
}
.navbar-options-icon:active {
  background-color: rgba(0, 64, 255, 0.3);
}

@media only screen and (max-width: 500px) {
  .navbar {
    font-size: 10px;
  }
  .navbar-logo {
    display: none;
  }
}
