.home-container {
  width: 100vw;
  height: 100vh;
  background-color: blue;
  color: white;
  font-family: governor;
  --bord-color: white;
}

.home-header {
  width: 4.5rem;
  height: 4.5rem;
  border-bottom: 2px dashed var(--bord-color);
}

.home-header-logo {
  padding: 0 10px;
  font-size: 3.5rem;
  animation: rot ease-in-out 5s 5s infinite;
  transform-origin: center;
}

@keyframes rot {
  100% {
    transform: rotate(360deg);
  }
}

.home-highlights {
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-highlights-heading {
  width: 65%;
  min-width: 375px;
  height: max-content;
  border-right: 3px dashed var(--bord-color);
  border-bottom: 3px dashed var(--bord-color);
}

.home-highlights-heading-text {
  padding: 0;
  font-size: 16vw;
  animation: move ease-in-out 1s 8s alternate forwards infinite;
}

@keyframes move {
  100% {
    transform: translate(5%, 0%);
  }
}

.home-highlights-subheading {
  font-size: 2rem;
  margin-top: 10px;
  padding: 5px 10px;
  border-bottom: 2px dashed var(--bord-color);
  border-left: 2px dashed var(--bord-color);
  /*border-right: 2px dashed white;*/
  cursor: pointer;
}

.home-highlights-subheading:hover {
  color: #f1f0f0;
}

.home-highlights-subheading:active {
  color: #cccbcc;
}
