.quiz-card {
  width: 100%;
  height: 18em; /*150px*/
  min-height: max-content;
  border-bottom: 2px dashed var(--acc-color);
  display: flex;
  align-items: center;
  cursor: pointer;
}

.quiz-card:hover {
  background-color: var(--hove-color);
}

.quiz-card:hover .quiz-card-title-image {
  background-color: navy;
}

.quiz-card:active {
  background-color: transparent;
}

.quiz-card-title-image {
  width: 1.6em;
  height: 1.9em;

  font-size: 7em;
  line-height: 1.8em;

  margin: 0 0.35em 0 0.35em;
  text-align: center;
  background-color: var(--acc-color);
  color: var(--back-color);
  box-shadow: rgba(0, 0, 0, 0.3) -2px 2px 10px 0 /*, inset rgba(0, 0, 0, 0.3) -3px 3px 15px 0px*/;

  text-shadow: rgba(0, 0, 0, 0.2) -5px 5px 1px;
  border-radius: 2px;
}

.quiz-card-info {
  color: var(--acc-color);
  /*letter-spacing: 0.5px;*/
}

.quiz-card-info-main {
  font-size: 3em;
  width: 65vw;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.quiz-card-info-sub {
  font-size: 2em;
  word-wrap: break-all;
}
