@font-face {
  font-family: governor;
  src: url(fonts/governor.ttf);
}

@font-face {
  font-family: bree;
  src: url(fonts/BreeSerifR.ttf);
}

@font-face {
  font-family: poppins;
  src: url(fonts/Poppins-SemiBold.ttf);
}

@font-face {
  font-family: averta;
  src: url(fonts/AvertaR.ttf);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline-style: none;
}

html {
  height: 100vh;
  width: 100vw;
  font-size: 15px;
}

body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

a {
  text-decoration-line: none;
  color: inherit;
}

.doodles-bg {
  background-image: url(images/DoodelsBG.svg);
}

.scales-bg {
  background-image: url(images/ScalesBG.svg);
}

.tornado-bg {
  background-image: url(images/TornadoBG.svg);
  background-size: cover;
  background-repeat: no-repeat;
}
