.quiz-pass-bg {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 255, 0.4);
  position: absolute;
  z-index: 4;
  font-size: 15px;
  color: blue;
  font-family: averta;
  display: flex;
  align-items: center;
  justify-content: center;
}

.quiz-pass-cont {
  font-size: 1.5em;
  background-color: white;
  border-radius: 2px;
  width: 20em;
  height: 12em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 10px 0px;
}

.quiz-pass-cont input {
  font-size: 1em;
  border-bottom: 1px solid blue;
  width: 70%;
  height: 1.5em;
  padding: 5px;
  color: blue;
  font-family: averta;
  text-align: center;
}

.quiz-pass-status {
  font-size: 0.8em;
}

.quiz-pass-cont button {
  font-size: 0.9em;
  font-family: averta;
  color: blue;
  cursor: pointer;
  padding: 3px 5px;
  border-radius: 2px;
  border: 1px solid blue;
  background-color: white;
  margin: 0px 10px;
}

.quiz-pass-cont button:hover {
  background-color: blue;
  color: white;
}

.quiz-pass-cont button:active {
  background-color: rgb(34, 34, 250);
}
