.bad-page-cont {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
}

.bad-page-text {
  font-size: 5em;
  width: 12em;
  padding: 1em 0px;
  font-family: poppins;
  text-align: center;
  color: white;
  background-color: transparent;
  word-wrap: break-word;
}

@media only screen and (max-width: 600px) {
  .bad-page-cont {
    font-size: 6px;
  }
}
