/*Make It Responsive*/
.quiz-board {
  width: 100vw;
  height: 100vh;
}

.quiz-cards {
  width: 100%;
  height: 91%;

  overflow-x: hidden;

  position: relative;
  z-index: 1;

  font-size: 9px;
  font-family: bree;

  --bord-color: rgba(256, 256, 256, 0.5);

  background-color: var(--back-color);
}

.quiz-board-loading {
  width: 100%;
  text-align: center;
  font-size: 2.5em;
  color: white;
  font-family: averta;
  margin-top: 10px;
  min-height: max-content;
}

.quiz-board-loading-gif {
  display: box;
  margin: 15px 50%;
}

.blue-quiz-cards {
  --back-color: blue;
  --acc-color: white;
  --hove-color: rgb(0, 70, 265);
}

.white-quiz-cards {
  --back-color: white;
  --acc-color: blue;
  --hove-color: rgb(240, 240, 240);
}

@media only screen and (max-width: 550px) {
  .quizes {
    font-size: 7px;
  }
}
