.quiz-score-bg {
  width: 100vw;
  height: 100vh;
  background-color: blue;

  display: flex;
  justify-content: center;
  align-items: center;
}

.quiz-score-cont {
  width: 90%;
  height: 90%;

  background-color: white;
  color: blue;

  border-radius: 2px;

  font-size: 10px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;

  box-shadow: rgba(0, 0, 0, 0.3) -5px 10px 10px 0px;
}

.quiz-score-title {
  text-align: center;
  width: 100%;
  height: 55%;
  font-size: 6em;
  font-family: bree;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  word-break: break-all;
}

.quiz-score-boxes {
  width: 100%;
  height: 35%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.quiz-score-box {
  min-width: 18em;
  width: 20%;
  height: 48%;
  text-align: center;

  font-family: averta;
  border: 2px solid blue;
  border-radius: 5px;
}

.quiz-score-box-text {
  width: 100%;
  height: 40%;
  font-size: 2.5em;
  border-bottom: 2px solid blue;
  word-break: break-all;
}

.quiz-score-box-num {
  width: 100%;
  height: 60%;
  font-size: 4em;
}

.quiz-score-btns {
  height: 10%;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
  min-height: 10em;
}

.quiz-score-btn {
  font-size: 2em;
  padding: 10px 20px;
  min-width: max-content;
  background-color: white;
  border: 2px solid blue;
  font-family: bree;
  color: blue;
  cursor: pointer;
  border-radius: 10px;
}

.quiz-score-btn:hover {
  background-color: blue;
  color: white;
}

.quiz-score-btn:active {
  background-color: rgb(48, 48, 253);
}

@media only screen and (max-width: 900px) {
  .quiz-score-cont {
    font-size: 7px;
  }
}

@media only screen and (max-width: 600px) {
  .quiz-score-cont {
    font-size: 6px;
  }
}

@media only screen and (max-width: 500px) {
  .quiz-score-cont {
    font-size: 5px;
  }
}

@media only screen and (max-height: 500px) {
  .quiz-score-cont {
    font-size: 5px;
  }
}
