.alert-box-text {
  position: absolute;
  z-index: 5;
  font-family: averta;
  font-weight: 100;
  top: 10px;
  left: 10px;
  padding: 10px;
  font-size: 18px;
  width: 250px;
  height: max-content;
  background-color: white;
  color: blue;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 5px 5px 0px;
  background-color: rgb(245, 245, 245);
  border-radius: 0px;
  opacity: 1;
}

.fade {
  animation: fade 4s forwards;
}

.display-off {
  display: none;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
