.quiz-create-option-box-cont {
  width: 100%;
  margin-bottom: 15px;
  height: 14em;
}

.quiz-create-option-box-cont textarea {
  width: 100%;
  font-size: 2.5em;
  min-height: max-content;
  height: 70%;
}

.quiz-create-option-box-cont div {
  width: 100%;
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.quiz-create-option-box-cont-btn {
  padding: 5px 10px;
  border-radius: 2px;
  background-color: white;
  color: blue;
  font-family: bree;
  font-size: 1.8em;
  cursor: pointer;
  width: 49.5%;
}

.quiz-create-option-box-cont-btn:hover {
  background-color: rgb(240, 240, 240);
  color: blue;
}

.quiz-create-option-box-cont-btn:active {
  background-color: rgb(230, 230, 230);
}

.quiz-create-option-box-right {
  color: white;
  background-color: #00cc66;
}

.quiz-create-option-box-right:hover {
  color: white;
  background-color: #00cc66;
}
