.quiz-admin-bg {
  background-color: white;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  font-family: averta;
}

.quiz-admin-cont {
  font-size: 1em;
  background-color: white;
  width: 40em;
  height: max-content;
  border-radius: 2px;
  border: 1px solid blue;
  box-shadow: rgba(0, 0, 0, 0.2) -5px 5px 10px 0px;
}

.quiz-admin-title {
  text-align: center;
  color: blue;
  border-bottom: 1px solid blue;
  width: 95%;
  margin: 5px auto;
}

.quiz-admin-search {
  font-size: 1.5em;
  border-bottom: 1px solid blue;
  margin: 10px auto;
  display: block;
  font-family: averta;
  color: blue;
  padding: 5px;
}

.quiz-admin-info {
  width: 95%;
  margin-left: 20px;
  margin-bottom: 10px;
  color: blue;
}

.quiz-admin-info p {
  font-size: 1.2em;
  margin-left: 5px;
}

.quiz-admin-info * {
  display: inline-block;
}

.quiz-admin-status {
  color: blue;
  text-align: center;
  font-size: 1.2em;
}

.quiz-admin-btn {
  margin: 5px auto;
  display: block;
  padding: 5px 10px;
  border: 1px solid blue;
  background-color: white;
  cursor: pointer;
  font-family: averta;
  color: blue;
  font-size: 1em;
  border-radius: 2px;
  margin-bottom: 10px;
}

.quiz-admin-btn:hover {
  color: white;
  background-color: blue;
}

.quiz-admin-btn:active {
  background-color: rgb(47, 47, 253);
}
